/* Define a common style for the landing div */
.landing {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 600px;
    background: linear-gradient(
      109.6deg,
      rgb(1, 24, 54) 11.2%,
      rgba(0, 37, 84, 0.8) 100.2%
    );
    @media (max-width: 767.98px) {
      flex-direction: column;
    }
  }
  
  /* Define a style for the left span */
  .left {
    flex: 1; /* Allow the left span to grow and shrink as needed */
    padding: 20px; /* Add some padding around the content */
    text-align: left; /* Align the text to the left */
    hr {
      background-color: yellow;
      border: 1px solid yellow;
      height: 1px;
      margin-right: 0.5em;
      width: 40%;
    }
    h4 {
      font-size: 1.35rem;
      font-style: italic;
      color: yellowgreen;
    }
    .titles {
      max-width: 700px;
      margin-left: 80px;
      margin-top: 7rem;
    }
    .caption1 {
      display: flex;
      align-items: center;
      gap: 8px; /* adds spacing between line and text */
    }
    .caption2 {
      p {
        font-size: 2.5rem;
        font-style: bold;
        color: white;
        text-transform: uppercase;
        span {
          color: yellow;
        }
      }
    }
    .caption3 {
      max-width: 450px;
      p {
        font-size: 1.25rem;
        font-weight: 200;
        font-style: oblique 40deg;
        color: white;
      }
    }
    .btns {
      display: flex;
      justify-content: space-between;
      max-width: 250px;
      margin-top: 20px;
    }
    @media (max-width: 767.98px) {
      padding: 5px;
      hr {
        width: 20%;
        border: 0.5px solid yellow;
      }
      h4 {
        font-size: 1rem;
      }
      .titles {
        max-width: 100%;
        margin-left: 5px;
        margin-top: 0.5rem;
      }
      .caption2 {
        p {
          font-size: 1.5rem;
        }
      }
      .caption3 {
        max-width: 100%;
        p {
          font-size: 0.85rem;
        }
      }
      .btns {
        max-width: 250px;
        margin-top: 15px;
      }
    }
  }
  
  /* Define a style for the right span */
.right {
  flex: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;

  img {
    width: 380px;              /* Adjust size as you prefer */
    height: 380px;
    border-radius: 50%;        /* Makes it circular */
    object-fit: cover;         /* Keeps face-centered crop */
    object-position: center;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
    border: 3px solid #fff;    /* Optional soft border */
  }

  @media (max-width: 767.98px) {
    img {
      width: 250px;
      height: 250px;
      border-radius: 50%;
    }
  }
}
